.app {
  // background-color: chartreuse;
  height: 100vh;

  .sections {
    width: 100vw;
    height: calc(100vh-70px);
    position: relative;
    top: 70px;
    scroll-snap-type: y mandatory;
    & ::-webkit-scrollbar {
      display: none;
    }

    > * {
      width: 100vw;
      // height: calc(100vh-70px);
      // position: relative;
      scroll-snap-align: start;
      overflow: hidden;
      scroll-behavior: smooth;
    }
  }
}
