@import "../../global.scss";

.top_bar {
  width: 100vw;
  height: 70px;
  background-color: whitesmoke;
  color: black;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 2s ease;

  .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 30px;
    flex-direction: row;
    width: 100%;

    .left_area {
      display: flex;
      flex-direction: row;
      align-items: center;

      .logo {
        font-size: 38px;
      }

      .nav_container {
        display: flex;
        flex-direction: row;
        justify-content: end;
        margin-left: 200px;

        @include mobile {
          display: none;
        }

        p {
          margin: 0px 50px;
        }
      }

      .logo {
        font-size: 40px;
        font-weight: 600;
        text-decoration: none;
      }
    }

    .right_area {
      .hamburger {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        span {
          width: 100%;
          height: 3px;
          background-color: black;
          transform-origin: left;
          transition: all 2s ease;
        }
      }
    }
  }

  &.active {
    background-color: black;

    .logo {
      color: white;
    }

    .nav_container {
      p {
        color: white;
      }
    }
    
    .hamburger {
      span {
        &:first-child {
          background-color: whitesmoke;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          background-color: whitesmoke;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
