@import "../../global.scss";

.menu_bar {
  background-color: black;
  height: 100vh;
  width: 260px;
  position: fixed;
  top: 0;
  right: -300px;
  z-index: 2;
  font-weight: 300;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s ease;

  &.active {
    right: 0;

    .menu_items {
      margin: 0;
      padding: 0;
      list-style: none;
      font-size: 25px;

      color: white;

      li {
        margin: 25px;

        a {
          font-size: inherit;
          color: inherit;
          text-decoration: none;
        }

        &:hover {
          color: blue;
        }
      }
    }
  }
}
