@import "../../global.scss";

.works_heading {
  @include mobile {
    height: 30px;
  }
}

.recent_works {
  background-color: rgba(0, 0, 0, 0.816);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100vw;
  height: 500px;

  @include mobile {
    height: 1320px;
    max-height: 1300px;
  }

  .arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;
    display: flex;
    justify-content: space-between;

    @include mobile {
      display: none;
    }

    &.left {
      left: 100px;
      transform: rotateY(180deg);
    }

    &.right {
      left: 1200px;
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    justify-content: center;
    transition: all 0.5s ease-out;
    // left: 75px;

    @include mobile {
      height: 100vh;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: -1;

      .item {
        width: 600px;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        align-items: center;

        @include mobile {
          width: 90%;
          height: 250px;
          margin: 20px 0;
          display: flex;
          justify-content: center;
        }

        .left {
          flex: 4;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            @include mobile {
              // justify-content: center;
              align-items: center;
              // height: 400px;
            }

            .imgContainer {
              width: 50px;
              height: 50px;
              border-radius: 50%;
              display: flex;
              align-items: center;
              justify-content: center;
              margin-top: -15px;
              margin-bottom: 15px;
              margin-left: 20px;

              @include mobile {
                margin-top: -25px;
                background: none;
                width: 80px;
                height: 80px;
                margin-left: 0;
              }

              img {
                width: 60px;
                height: 60px;
                border-radius: 50%;
                border: 3px rgb(245, 179, 155);

                @include mobile {
                  width: 60px;
                  height: 60px;
                  // margin-bottom: 20px;
                  border-radius: 50%;
                }
              }
            }

            h2 {
              font-size: 0.6rem;
              margin-top: 5px;
              font-weight: bold;

              @include mobile {
                font-size: 15px;
                text-align: center;
              }
            }
            p {
              font-size: 13px;
              font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
              font-weight: 600;
              @include mobile {
                text-align: center;
              }
            }

            .project_links {
              display: flex;
              flex-direction: row;
              border-radius: 0px 20px 20px 0px;

              @include mobile {
                background-color: rgba(0, 0, 0, 0.864);
                flex-direction: row;
                width: 270px;
                justify-content: space-between;
                border-radius: 9px;
                align-items: center;
                padding: 0px 13px 6px 11px;
                margin-top: 20px;
              }
              @include below_350px {
                margin-top: 0px;
              }

              .second {
                margin-left: 6px;

                @include mobile {
                  margin: 0;
                }
              }
              span {
                font-size: 15px;
                font-weight: 700;
                text-decoration: underline;
                cursor: pointer;
                color: white;
                background-color: black;
                padding: 7px;
                border-radius: 20px;
                font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;

                @include mobile {
                  color: white;
                  padding: 0;
                  margin: 0;
                }

                &:hover {
                  background-color: green;
                  transform: translateY(0) scale(1.2);
                  // color: black;
                }
              }
            }
          }
        }
        .right {
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;

          @include mobile {
            display: none;
          }

          img {
            width: 400px;
            transform: rotate(-10deg);
          }
        }
      }
    }
  }
}

.recent_works::before {
  content: "";
  background-image: url("../assets/works_big_back.png");
  background-position: "center";
  background-size: cover;
  background-repeat: "no-repeat";
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  @include mobile {
    background: none;
    height: 1320px;
    max-height: 1300px;
    opacity: 0.5;
    background-image: url("../assets/works_back.png");
    background-position: "center";
    background-size: cover;
    background-repeat: "no-repeat";
  }
}
